import hero from '../components/hero';
import { waitList } from '../components/waitlist';

const dynamicSectionsLoad = async () => {
	const power = await import('../components/power');
	power.power();
	const extracted = await import('../components/extracted');
	extracted.extracted();
	const list = await import('../components/list');
	list.list();
};

const index = () => {
	hero();
	waitList();

	let isScrolled = false;
	window.addEventListener('scroll', () => {
		if (!isScrolled) {
			isScrolled = true;
			dynamicSectionsLoad();
		}
	});
};

export default index;
